.container {
    height:100vh;
}

.list-items {
    display:flex;
    padding:20px;
    justify-content: space-around;
}

.list-items li {
    display:inline-block;
    list-style:none;
    font-weight: bold;
    border-bottom: 2px solid #039be5;
    cursor:pointer;
}



.list-item-content {
    margin-top: 10%;
}